<template>
  <div class="g-apply">
    <div class="m-name">
      <span class="s-msg">商家名称：</span>
      <input type="text" v-model="bit.name" placeholder="请输入商家公司或店铺名称">
    </div>
    <div class="m-img">
      <span class="s-msg">商家图片：</span>
      <cube-upload
              ref="upload"
              v-model="files"
              :action="action"
              @files-added="addedHandler"
              @file-error="errHandler"
              @file-success="sussHandler">
        <div class="clear-fix">
          <cube-upload-file v-for="(file, i) in files" :file="file" :key="i"></cube-upload-file>
          <cube-upload-btn :multiple="false">
            <img v-if="bit.icon" :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+bit.icon" alt="">
            <img v-else src="../assets/images/addimg.png" alt="">
          </cube-upload-btn>
        </div>
      </cube-upload>
    </div>
    <div class="m-area">
      <span class="s-msg">商家区域：</span>
      <div class="s-navChoose">
        <div @click="showAddressPicker" class="beyond">{{bit.province||'请选择省份'}}</div>
        <div @click="showAddressPicker" class="beyond">{{bit.city||'请选择城市'}}</div>
        <div @click="showAddressPicker" class="beyond">{{bit.area||'请选择区域'}}</div>
      </div>
    </div>
    <div class="m-area">
      <span class="s-msg">商家地址：</span>
      <input type="text" v-model="bit.address" placeholder="请输入商家详细地址">
    </div>
    <div class="m-class">
      <span class="s-msg">经营类目：</span>
      <div @click="showcategory">{{bit.sort||'请选择经营类目'}}</div>
    </div>
    <div class="m-people">
      <span class="s-msg">产品名称：</span>
      <input type="text" v-model="bit.proName" placeholder="请输入产品名称">
    </div>
    <div class="m-people">
      <span class="s-msg">联系人：</span>
      <input type="text" v-model="bit.contact" placeholder="请输入联系人">
    </div>
    <div class="m-phone">
      <span class="s-msg">联系电话：</span>
      <input type="text" v-model="bit.phone" placeholder="请输入联系电话">
    </div>
    <div class="m-ps">
      <span class="s-msg">商家介绍：</span>
      <textarea cols="30" rows="10" v-model="bit.intro" placeholder="请输入商家介绍"></textarea>
    </div>
    <div class="m-submit" @click="submitMsg">提交申请</div>
  </div>
</template>

<script>
  import {provinceList, cityList, areaList} from '../assets/js/area'

  const addressData = provinceList
  addressData.forEach(province => {
    province.children = cityList[province.value]
    province.children.forEach(city => {
      city.children = areaList[city.value]
    })
  })
  export default {
    name: "apply",
    data(){
      return {
        action:{ target: this.$bee+'/api/upload/uploadFile',fileName:'attach' },
        files: [],
        sorts:[],/*类目数据*/
        bit:{
          name:'',
          contact:'',
          phone:'',
          intro:'',
          icon:'',
          address:'',
          proName:'',
          province:'',
          city:'',
          area:'',
          provinceId:'',
          cityId:'',
          areaId:'',
          sort:'',
          sortId:'',
          username:sessionStorage.getItem('userid')
        }
      }
    },
    created(){
      this.sorting();/*经营类目*/

    },
    mounted () {
      this.addressPicker = this.$createCascadePicker({
        title: '地址选择',
        data: addressData,
        onSelect: this.selectHandle,
        onCancel(){
          console.log('取消')
        }
      })
    },
    methods: {
      applyMsg(){
        this.$post(this.$bee+'/api/shop/leagueShopDetail',{username:this.bit.username}).then((data)=>{
          if(data.code==200){
            this.bit={
                name:data.data.shop.name,
                contact:data.data.shop.contact,
                phone:data.data.shop.phone,
                intro:data.data.shop.intro,
                icon:data.data.shop.icon,
                address:data.data.shop.address,
                proName:data.data.shop.proName,
                province:data.data.shop.province,
                city:data.data.shop.city,
                area:data.data.shop.area,
                provinceId:data.data.shop.provinceId,
                cityId:data.data.shop.cityId,
                areaId:data.data.shop.areaId,
                sort:'',
                sortId:data.data.shop.sortId,
                username:sessionStorage.getItem('userid')
              }
            this.sorts.forEach((i)=>{
              if(i.value==data.data.shop.sortId){
                this.bit.sort=i.text;
              }
            })
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      sorting(){
        this.$post(this.$bee+'/api/shop/listSort').then((data)=>{
          if(data.code==200){
            data.data.sorts.forEach((i)=> {
              this.sorts.push({text:i.name,value:i.id});
            })
            this.applyMsg();/*获取信息*/
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      addedHandler() {/*上传图片*/
        const file = this.files[0]
        file && this.$refs.upload.removeFile(file)
      },
      errHandler(file) {/*上传图片失败*/
        // const msg = file.response.message
        this.$createToast({
          type: 'warn',
          txt: file.response.message,
          time: 1000
        }).show()
      },
      sussHandler(data){/*上传图片成功*/
        if(data.response.data.pics[0]){
          this.bit.icon=data.response.data.pics[0];
        }else{
          this.$createToast({
            txt: data.response.message,
            type: 'txt'
          }).show();
        }
      },
      showcategory(){/*选择类目*/
        var _this = this;
        this.$createPicker({
          title: '请选择区域',
          data: [_this.sorts],
          onSelect(v, i, t) {
            _this.bit.sortId=v[0];
            _this.bit.sort=t.join(', ');
          },
          onCancel() {
            console.log('取消')
          }
        }).show()
      },
      showAddressPicker() {
        this.addressPicker.show()
      },
      selectHandle(selectedVal, selectedIndex, selectedText) {
        this.bit.province=selectedText[0];
        this.bit.city=selectedText[1];
        this.bit.area=selectedText[2];
        this.bit.provinceId=selectedVal[0];
        this.bit.cityIdId=selectedVal[1];
        this.bit.areaId=selectedVal[2];
      },
      submitMsg(){
        let _do=true;
        Object.keys(this.bit).forEach((i)=>{
          if(this.bit[i]===''){
            this.$createToast({
              txt: '请将信息填写完整！',
              time: 1000,
              type: 'warn'
            }).show()
            _do=false;
          }
        })
        if(!_do){
          return ;
        }
        this.$post(this.$bee+'/api/shop/applyLeagueShop',this.bit,{showLoading:true,parsed:false}).then((data)=>{
          if(data.code==200){
            this.$createToast({
              txt: data.message,
              time: 2000,
              type: 'correct',
              onTimeout: () => {
                this.$router.go(-1)
              }
            }).show()
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
.g-apply
  padding 15px
  text-align left
  margin-bottom 50px
  .m-class
    div
      text-indent 5px
      width: 255px;
      height: 20px;
      line-height: 20px;
      border: solid 1px #c8c8c8;
      font-size 15px
      display inline-block
  .s-navChoose
    display inline-block
    vertical-align: sub
    &>div
      text-indent 5px
      width: 80px;
      height: 20px;
      line-height: 20px;
      border: solid 1px #c8c8c8;
      font-size 15px
      display inline-block
      margin-right:5px
  &>div
    margin-bottom 18px
    span,input
      vertical-align middle
    span
      display inline-block
      width 75px
      height 20px
      line-height 20px
      color: #323232
      font-size 15px
    input,textarea
      text-indent 5px
      width: 255px;
      height: 20px;
      line-height: 20px;
      border: solid 1px #c8c8c8;
      font-size 15px
    textarea
      height 100px
      vertical-align top
      resize:none;
    &.m-img
      img
        width 70px
        height 70px
        vertical-align top
  .m-submit
    width 375px
    height 49px
    line-height 49px
    background-color #db3838
    color: #fcfcfc;
    font-size: 28px;
    position fixed
    bottom 0
    left 0
    margin 0
    padding 0
    text-align center
.cube-upload
  display inline-block
  vertical-align top
  width 70px
  height 70px
  .cube-upload-file, .cube-upload-btn
    margin: 0
    width 70px
    height 70px
  .cube-upload-file
    margin: 0
    + .cube-upload-btn
      margin-top: -70px
      opacity 0
  >>>.cube-upload-file-def
    width: 100%
    height: 100%
    .cubeic-wrong
      display: none
</style>
